/**
* Function to convert image data to corresponding gray values
* @param {ImageData} imgdata 
* @param {Number} size Size of the canvas in RGBA format -> CanvasWidth * CanvasHeight * 4
*/
export function grayscale(imgdata, size){
   for(let i=0; i<size; i+=4){
       // Weighted method to convert RGB to grayscale, taking wavelength into consideration (luminosity method)
       let grayval = ( (0.3 * imgdata[i]) + (0.59 * imgdata[i+1]) + (0.11 * imgdata[i+2]) )
       //adjust tightly packed RGB(A) values in ImageData buffer
       imgdata[i] = grayval;
       imgdata[i+1] = grayval;
       imgdata[i+2] = grayval;
   }
}