<template>
   <v-card
      outlined
      :width="'100%'"
   >
      <v-container fluid>
         <v-row>
         <v-col>
            <div>
               Sample Rate: {{ sampleRate }}%
            </div>
            <v-slider
               v-model="sampleRate"
               :max="20"
               :min="1"
               @change="updateSampleRate"
            >
            </v-slider>
         </v-col>
      </v-row>
       <v-row>
         <v-col>
            <div>
               Edge Threshold: {{ edgeThreshold }}
            </div>
            <v-slider
               v-model="edgeThreshold"
               :max="255"
               :min="70"
               @change="updateEdgeThreshold"
            >
            </v-slider>
         </v-col>
      </v-row>
      <v-row>
         <v-col>
            <v-expansion-panels flat hover tile>
               <v-expansion-panel>
               <v-expansion-panel-header>Details</v-expansion-panel-header>
               <v-expansion-panel-content>
                  <v-simple-table>
                        <tbody>
                           <tr>
                              <td>
                                 <v-icon>{{ icons.triangle }}</v-icon>
                                 <span>Polygons</span>
                              </td>
                              <td id="polygons">N/A</td>
                           </tr>
                           <tr>
                              <td>
                                 <v-icon>{{ icons.vertex }}</v-icon>
                                 <span>Vertices</span>
                              </td>
                              <td id="vertices">N/A</td>
                           </tr>
                           <tr>
                              <td>
                                 <v-icon>{{ icons.edge }}</v-icon>
                                 <span>Edge</span>
                              </td>
                              <td id="edges">N/A</td>
                           </tr>
                        </tbody>
                  </v-simple-table>
               </v-expansion-panel-content>
            </v-expansion-panel>
            </v-expansion-panels>
         </v-col>
      </v-row>
      <v-row>
         <v-col>
            <v-btn
               depressed
               text
               color="primary"
               ripple
               @click="polygonizeHandler"
            >
            <v-icon>{{ icons.polymorph }}</v-icon>
               Polymorph
            </v-btn>
            <v-btn
               depressed
               text
               color="primary"
               ripple
               @click="downloadHandler"
            >
               <v-icon>{{ icons.download }}</v-icon>
               <span>Download</span>
            </v-btn>
         </v-col>
      </v-row>
      </v-container>
   </v-card>
</template>

<script>
import {
   mdiTriangle,
   mdiTransitConnection ,
   mdiRayVertex,
   mdiVectorTriangle,
   mdiDownload 
} from '@mdi/js'
export default {
   name: 'Configuration',
   data: () => ({
      sampleRate: 0,
      edgeThreshold: 100,
      icons: {
         download: mdiDownload,
         polymorph: mdiVectorTriangle,
         triangle: mdiTriangle,
         vertex: mdiRayVertex,
         edge: mdiTransitConnection
      }
   }),
   methods: {
      polygonizeHandler() {
         this.$emit('polygonize', null)
      },
      downloadHandler() {
         this.$emit('download', null)
      },
      updateSampleRate() {
         this.$emit('updateSampleRate', this.sampleRate * 0.01)
      },
      updateEdgeThreshold() {
         this.$emit('updateEdgeThreshold', this.edgeThreshold)
      }
   }
}
</script>