/**
* Function to sample potential vertices for triangulation
* @param {ImageData} imgdata 
* @param {Number} width 
* @param {Number} height 
* @param {Number} samplerate Float (0.0-1.0) which describes percentage of elements to be sampled
* @param {Number} threshold Pixel value threshold at which a pixel is considered intersting and therefore a potential vertex (0-255)
*/
export function sample(imgdata, width, height, samplerate, threshold){

/*
   //const canvas = document.createElement('canvas')
   const canvas = document.getElementById('canv')
   canvas.width = width
   canvas.height = height
   const ctx = canvas.getContext('2d')
   ctx.fillStyle = "white";
   ctx.fillRect(0, 0, canvas.width, canvas.height);
   console.log(ctx.getImageData(0,0,width, height))
*/

   let size = width*height*4;
   let pixels = [];
   //iterate over pixels and look for potential vertices
   for(let i=0; i<size; i+=4){
      if(imgdata[i] > threshold){
         imgdata[i] = 255;
         imgdata[i+1] = 0;
         imgdata[i+2] = 0;
/*
         if(ctx.getImageData((i/4) % width, Math.ceil((i/4)/width), 1, 1).data[0] === 255) {
            console.log(ctx.getImageData((i/4) % width, Math.ceil((i/4)/width), 1, 1).data[0])
            ctx.beginPath()
            ctx.fillStyle = "#000000";
            ctx.arc((i/4) % width, Math.ceil((i/4)/width), 10, 0, 2 * Math.PI)
            ctx.fill()
            ctx.closePath()
            ctx.beginPath()
            ctx.fillStyle = "#FF0000";
            ctx.arc((i/4) % width, Math.ceil((i/4)/width), 5, 0, 2 * Math.PI)
            ctx.fill()
            ctx.closePath()
            pixels.push([(i/4) % width, Math.ceil((i/4)/width)]);
         }
*/
         //push array of x,y coordinates (canvas' format) to pixels buffer
         pixels.push([(i/4) % width, Math.ceil((i/4)/width)]);
      }
   }

   //randomly shuffle vertices in pixels buffer
   for(let i=pixels.length-1; i>0; i--){
      let tmp = pixels[i];
      let j = Math.floor(Math.random() * (i+1))
      pixels[i] = pixels[j];
      pixels[j] = tmp;
   }

   //slice array according to samplerate
   pixels = pixels.slice(0, Math.floor(pixels.length*samplerate))
   //push extra vertex for every corner to avoid image to not be polygonized completely
   pixels.push([0,0], [0, height], [width, 0], [width, height])

   return pixels;
}
