<template>
   <div id="file-input-wrapper">
      <v-file-input
         dense
         show-size
         filled
         hide-details
         accept="image/*"
         label="File Input"
         @change="imageChangedHandler"
      ></v-file-input>
   </div>
</template>

<script>
export default {
   name: 'ImageUpload',
   data: () => ({

   }),
   methods: {
      imageChangedHandler(event) {
         this.$emit('updateImage', URL.createObjectURL(event))
      }
   }
}
</script>
<style scoped>
   #file-input-wrapper {
      width: 100%;
      background: white;
      border-radius: 2px;
   }
</style>