<template>
   <v-app id="main">
      <Header></Header>
      <v-container fluid>
         <v-row justify="center">
            <v-col cols="12" md="6" sm="6">
               <ImageUpload
                  @updateImage="updateImage"
               />
            </v-col>
         </v-row>
         <v-row>
            <v-col cols="12" md="6" sm="12">
               <Picture
                  :img="xd"
                  @updateImage="updateImage"
               ></Picture>
            </v-col>
            <v-col cols="12" md="6" sm="12">
               <Configuration
                  @updateSampleRate="sampleRate = $event"
                  @updateEdgeThreshold="edgeThreshold = $event"
                  @polygonize="onPolygonizeEvent"
                  @download="onDownloadEvent"
               ></Configuration>   
            </v-col>
         </v-row>
      </v-container>
   </v-app>
</template>

<script>
import Header from './components/Header'
import Picture from './components/Picture'
import Configuration from './components/Configuration'
import ImageUpload from './components/ImageUpload'
import { sobel } from './helpers/sobel'
import { polymorph } from './helpers/polymorph'
import { mdiGithub } from '@mdi/js';
export default {
   name: 'App',
   components: {
      Header,
      Picture,
      Configuration,
      ImageUpload
   },
   data: () => ({
      iconz: mdiGithub,
      cachedImgBufferData: null,
      img: null,
      imgBuffer: null,
      imgBufferColor: null,
      context: null,
      imgBufferData: null,
      imgSize: null,
      xd: null,
      sampleRate: 0.01,
      edgeThreshold: 100
   }),
   created() {
      const imgBuffer = document.createElement('canvas')
      const context = imgBuffer.getContext('2d')
      this.imgBuffer = imgBuffer
      this.context = context
   },
   methods: {
      updateImage(event) {
         this.img = event
         const img = new Image()
         img.src = this.img
         const vm = this
         img.onload = function() {
            vm.imgBuffer.width = img.width
            vm.imgBuffer.height = img.height
            vm.context.drawImage(img, 0, 0)
            vm.imgBufferData = vm.context.getImageData(0, 0, vm.imgBuffer.width, vm.imgBuffer.height)
            vm.cachedImgBufferData = new Uint8ClampedArray(vm.imgBufferData.data)
            vm.imgBufferColor = [...vm.imgBufferData.data]
            vm.imgSize = vm.imgBufferData.data.length
            vm.xd = vm.imgBuffer.toDataURL()
         }
      },
      rollbackChanges() {
         this.imgBufferData.data.set(this.cachedImgBufferData)
         this.context.putImageData(this.imgBufferData, 0, 0)
         this.xd = this.imgBuffer.toDataURL()
      },
      onDownloadEvent() {
         const download = document.createElement('a')
         this.imgBuffer.toBlob((blob) => {
            download.href = URL.createObjectURL(blob)
            download.download = 'polygonize.jpg'
            download.click()
         }, 'image/jpg', 1.0)
      },
      onPolygonizeEvent() {
         this.rollbackChanges()
         const vm = this
         sobel(vm.imgBufferData.data, vm.imgBuffer.width, vm.imgBuffer.height)
         vm.context.putImageData(vm.imgBufferData, 0, 0)
         polymorph(vm.imgBufferData.data, vm.imgBuffer.width, vm.imgBuffer.height, vm.context, vm.imgBufferColor, vm.sampleRate, vm.edgeThreshold)
         vm.xd = vm.imgBuffer.toDataURL()
      }
   }
}
</script>

<style scoped>
   #main {
      background: linear-gradient(0deg, #B388FF 0%, #6200EA 70%);
   }
</style>