import { grayscale } from './grayscale'

/**
* Function to apply Sobel filter to ImageData by convoluting sobel operator on x and y direction
* @param {ImageData} imgdata 
* @param {Number} width Width (in pixels) of Canvas Element
* @param {Number} height Height (in pixels) of Canvas Element
*/
export function sobel(imgdata, width, height){
   //Sobel operators for x and y axis
   let sobelX = [[1,0,-1],
                 [2,0,-2],
                 [1,0,-1]];
   let sobelY = [[1,2,1],
                 [0,0,0],
                 [-1,-2,-1]];

   //array to store RGBA color values of filteres pixels
   let sobel_values = new Uint8ClampedArray(width*height*4);
   //index variable
   let idx = 0;
   //grayscale image before applying filter
   grayscale(imgdata, width*height*4);

   let width_rgba = width*4;

   for(let j=0; j<height; j++){
       for(let i=0; i<width_rgba; i+=4){
           //since sobel edge-detection cant be applied to outer pixels of image, we ignore them for now
           if(i!=0 || i!= width_rgba-4 || j!=0 || j!=height-1){
               //apply X sobel operator to current pixel
               const sobelX_pixel = imgdata[i+width_rgba*j-4-width_rgba] * sobelX[0][0] + 
                           imgdata[i+width_rgba*j+4-width_rgba]    * sobelX[0][2] +
                           imgdata[i+width_rgba*j-4]               * sobelX[1][0] +
                           imgdata[i+width_rgba*j+4]               * sobelX[1][2] +
                           imgdata[i+width_rgba*j-4+width_rgba]    * sobelX[2][0] + 
                           imgdata[i+width_rgba*j+4+width_rgba]    * sobelX[2][2];

               //apply Y sobel operator to current pixel
               const sobelY_pixel = imgdata[i+width_rgba*j-4-width_rgba] * sobelY[0][0] + 
                           imgdata[i+width_rgba*j-width_rgba]      * sobelY[0][1] +
                           imgdata[i+width_rgba*j+4-width_rgba]    * sobelY[0][2] +
                           imgdata[i+width_rgba*j-4+width_rgba]    * sobelY[2][0] + 
                           imgdata[i+width_rgba*j+width_rgba]      * sobelY[2][1] + 
                           imgdata[i+width_rgba*j+4+width_rgba]    * sobelY[2][2];

               //insert filtered RGBA values to buffer
               for(let k=0; k<4; k++){
                   if(k<3){
                       sobel_values[idx] = Math.floor(Math.sqrt(Math.pow(sobelX_pixel,2) + Math.pow(sobelY_pixel, 2)));
                   } else {
                       //since we didnt adjust Alpha values, we just copy them
                       sobel_values[idx] = imgdata[i+width_rgba*j+3];
                   }
                   idx++;
              }

           } else{
               //just copy un-filtered pixels (outer pixels)
               for(let k=0; k<4; k++){
                   sobel_values[idx] = imgdata[i+width_rgba*j+k];
               }
           }
       }
   }
   //assign filtered RGBA values to Image Buffer
   for(let i=0; i<sobel_values.length; i++){
       imgdata[i] = sobel_values[i];
   }
}