<template>
   <div>
      <v-img
         id="img"
         :src="img || imageUrl"
         contain
         :width="'100%'"   
      ></v-img>
      <img id="lol" src="" alt="">
   </div>
</template>

<script>
export default {
   name: 'Picture',
   data() {
      return {
         imageUrl: ''// require('../../pic.jpg')
      }
   },
   props: {
      img: {
         type: String,
         default: () => ''
      }
   },
   methods: {
      imageChangedHandler(event) {
         const file = URL.createObjectURL(event)
         this.imageUrl = file
         this.$emit('updateImage', file)
      }
   }
}
</script>