import Delaunator from 'delaunator'
import { sample } from './sample'

/**
* Function to triangulate (delaunay) sampled image and draw triangles
* @param {ImageData} imgdata 
* @param {Number} width 
* @param {Number} height 
* @param {CanvasRenderingContext2D} ctx 
* @param {Array} colorbuf 
*/
export function polymorph(imgdata, width, height, ctx, colorbuf, sr, et){
   const pixels = sample(imgdata, width, height, sr, et);
   //apply delaunay algorithm to array of sampled vertices
   const delaunay = Delaunator.from(pixels);
   //connect tightly packed vertices indices by drawing a path
   for(let i=0; i<delaunay.triangles.length; i+=3){
       ctx.beginPath()
       ctx.moveTo(pixels[delaunay.triangles[i]][0], pixels[delaunay.triangles[i]][1])
       ctx.lineTo(pixels[delaunay.triangles[i+1]][0], pixels[delaunay.triangles[i+1]][1])
       ctx.lineTo(pixels[delaunay.triangles[i+2]][0], pixels[delaunay.triangles[i+2]][1])
       ctx.closePath();

       //calculate mid-point of drawn triangle (needed to detect triangles fill color, which is defined by center pixel's color value)
       const midX = Math.floor((pixels[delaunay.triangles[i]][0] + pixels[delaunay.triangles[i+1]][0] + pixels[delaunay.triangles[i+2]][0]) / 3)
       const midY = Math.floor((pixels[delaunay.triangles[i]][1] + pixels[delaunay.triangles[i+1]][1] + pixels[delaunay.triangles[i+2]][1]) / 3)
       //convert canvas' pixel coordinate to corresponding ImageData's buffer index
       const mid_rgba = midY * width * 4 + midX * 4;

       //set fill and stroke colors and fill/stroke
       ctx.fillStyle = `rgb(${colorbuf[mid_rgba]},${colorbuf[mid_rgba+1]},${colorbuf[mid_rgba+2]})`
       ctx.fill();
       ctx.lineWidth = 1;
       ctx.strokeStyle = `rgb(${colorbuf[mid_rgba]},${colorbuf[mid_rgba+1]},${colorbuf[mid_rgba+2]})`
       ctx.stroke()
   }

   //document.getElementById('polygons').innerHTML = delaunay.triangles.length / 3
   //document.getElementById('vertices').innerHTML = pixels.length
   //document.getElementById('edges').innerHTML = delaunay.triangles.length
}